<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <div class="w_200">
                    <el-input placeholder="请输入品牌" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                </div>
                <!--  <div class="m_l_20">
                        <el-button @click="search">查询</el-button>
                    </div> -->
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建车型</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="70">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="brand" label="品牌">
                    </el-table-column>
                    <el-table-column align="center" prop="brand" label="品牌图片" width="100">
                        <template scope="scope">
                            <img :src="scope.row.brandImage" class="viewImg">
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="carSeries" label="车系">
                    </el-table-column>
                    <el-table-column align="center"  label="颜色">
                        <template scope="scope">
                            <span v-html="parseJsonArray(scope.row.color)"></span>
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="modelYear" label="年款">
                    </el-table-column>
                    <el-table-column align="center" prop="displacement" label="排量">
                    </el-table-column>
                    <el-table-column align="center" prop="seatNumber" label="座位数">
                    </el-table-column>
                    <el-table-column align="center" prop="clutch" label="离合器">
                    </el-table-column>
                    <el-table-column align="center" prop="operatorName" label="添加人">
                    </el-table-column>
                    <el-table-column align="center" prop="createAt.time" label="添加时间" width="140" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="180">
                        <template scope="scope">
                            <el-button size="small" type="info" @click="editItem(scope.$index, scope.row)" >查看</el-button>
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <!-- <el-dialog class="edit dialog-info" title="查看" :visible.sync="editDialog"  @close="resetFields('editForm', 'editPicFile')"> -->
            <!-- <el-form :model="eForm" label-position="right" label-width="110px" :rules="rules" ref="editForm"> -->
        <el-dialog class="edit dialog-info" title="查看" :visible.sync="editDialog">
            <el-form :model="eForm" label-position="right" label-width="110px">
                <el-form-item label="品牌：" prop="brand">
                    <el-input type="text" v-model="eForm.brand" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>

                <el-form-item label="车系：" prop="carSeries">
                    <el-input type="text" v-model="eForm.carSeries" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>

                <el-form-item label="品牌图片：" >
                    <img :src="eForm.brandImage" style="width:100px;height:100px">
                </el-form-item>

                
                <!-- <el-form-item class="upload-box" label="品牌图片：" prop="pic">
                    <el-upload
                      class="avatar-uploader"
                      :action="API + 'media/file_upload'"
                      ref="editPicFile"
                      :show-file-list="false"
                      :on-success="onSuccess">
                      <img v-if="picUrlList" :src="picUrlList" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                </el-form-item> -->

                <el-form-item label="颜色：">
                    <span v-for="tag in eForm.color" :key="tag">{{tag}}　</span>
                </el-form-item>

                <!-- <el-form-item label="颜色：">
                    <el-tag 
                        :key="tag" 
                        v-for="tag in eForm.color" 
                        :closable="true" 
                        :close-transition="false" 
                        @close="handleCloseEditColor(tag)"
                    >
                        {{tag}}
                    </el-tag>
                    <el-input 
                        class="input-new-tag" 
                        v-if="inputVisible" 
                        v-model="inputValue" 
                        ref="saveTagInput" 
                        size="mini" 
                        @keyup.enter.native="editInputConfirm" 
                        @blur="editInputConfirm" 
                        style="width: 68px; height: 30px"
                    >
                    </el-input>
                    <el-button v-else class="button-new-tag" size="small" @click="showInput">添加颜色</el-button>
                </el-form-item> -->

                <el-form-item label="年款：" prop="modelYear">
                    <el-input type="text" v-model="eForm.modelYear" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>
                <el-form-item label="排量：" prop="displacement">
                    <el-input type="text" v-model="eForm.displacement" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>
                <el-form-item label="座位数：" prop="seatNumber">
                    <el-input type="number" v-model="eForm.seatNumber" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>
                <el-form-item label="离合器：" prop="clutch">
                    <el-input type="text" v-model="eForm.clutch" :readonly='readonlyToggle'>
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <!-- <el-button @click="editDialog = false">取 消</el-button> -->
                <!-- <el-button type="primary" @click="editSave('editForm')">确 定</el-button> -->
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>
    
        <el-dialog class="add dialog-info" title="新建车型" :visible.sync="addDialog" :close-on-click-modal="false" @close="resetFields('addForm', 'addPicFile')">
            <el-form :model="aForm" label-position="right" label-width="90px" :rules="rules" ref="addForm">
                <el-form-item label="品牌：" prop="brand">
                    <el-input type="text" v-model="aForm.brand">
                    </el-input>
                </el-form-item>

                <el-form-item class="upload-box" label="品牌图片：" prop="pic">
                    <el-upload
                      class="avatar-uploader"
                      :action="API + 'media/file_upload'"
                      ref="addPicFile"
                      :show-file-list="false"
                      :on-success="onSuccess">
                      <img v-if="picUrlList" :src="picUrlList" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <!-- <p class="tips" style="font-size: 12px;color: #9a9a9a;">*图片上传规格：350*350</p> -->
                </el-form-item>

                <el-form-item label="车系：" prop="carSeries">
                    <el-input type="text" v-model="aForm.carSeries">
                    </el-input>
                </el-form-item>
                <el-form-item label="颜色：">
                    <el-tag 
                        :key="tag" 
                        v-for="tag in aForm.color" 
                        :closable="true" 
                        :close-transition="false" 
                        @close="handleCloseAddColor(tag)"
                    >
                        {{tag}}
                    </el-tag>
                    <el-input 
                        class="input-new-tag" 
                        v-if="inputVisible" 
                        v-model="inputValue" 
                        ref="saveTagInput" 
                        size="mini" 
                        @keyup.enter.native="addInputConfirm" 
                        @blur="addInputConfirm" 
                        style="width: 68px; height: 30px"
                    >
                    </el-input>
                    <el-button v-else class="button-new-tag" size="small" @click="showInput">添加颜色</el-button>
                </el-form-item>

                <el-form-item label="年款：" prop="modelYear">
                    <el-input type="text" v-model="aForm.modelYear">
                    </el-input>
                </el-form-item>
                <el-form-item label="排量：" prop="displacement">
                    <el-input type="text" v-model="aForm.displacement">
                    </el-input>
                </el-form-item>
                <el-form-item label="座位数：" prop="seatNumber">
                    <el-input type="number" v-model="aForm.seatNumber">
                    </el-input>
                </el-form-item>
                <el-form-item label="离合器：" prop="clutch">
                    <el-input type="text" v-model="aForm.clutch">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addSave('addForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {

        // 正整数
        const positiveIntegerNum = function (rule, value, callback) {
            if (/^([1-9]\d*)$/.test(value)) {
                callback();
            } else {
                callback(new Error('请输入正确数值'));
            }
        }
        // 正数
        const positiveNum = function (rule, value, callback) {
            if (/^([1-9]\d*\.\d{1,2}|0\.\d{1,2}|[1-9]\d*)$/.test(value)) {
                callback();
            } else {
                callback(new Error('请输入正确数值'));
            }
        }
        const checkPic = (rule, value, callback) => {
            if (!this.picUrlList) {
              callback(new Error('请上传图片后重试'))
            } else {
              callback()
            }
        }
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            editDialog: false, //弹出框-编辑
            addDialog: false, //弹出框-新建
            readonlyToggle: true,// 设置只读
            // 编辑-表单
            eForm: {
                carModelUuid: 0,
                brand: '',
                color: '',
                modelYear: '',
                carModel: '',
                carSeries: '',
                displacement: '',
                seatNumber: '',
                clutch: '',
                color: [],
            },
            // 添加-表单
            aForm: {
                brand: '',
                color: '',
                modelYear: '',
                carModel: '',
                carSeries: '',
                displacement: '',
                seatNumber: '',
                clutch: '',
                color: [],
            },
            // 标签按钮
            inputVisible: false,
            // 标签输入的值
            inputValue: '',
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,

            // 上传预览
            dialogVisible: false,
            // 预览文件链接
            picUrl: null,
            // 上传文件链接
            picUrlList: null,

            rules: {
                pic: [{ validator: checkPic, trigger: 'change' }],
                brand: [{required: true, message: '不能为空', trigger: 'blur'}],
                carSeries: [{required: true, message: '不能为空', trigger: 'blur'}],
                color: [{required: true, message: '不能为空', trigger: 'blur'}],
                modelYear: [{required: true, message: '不能为空', trigger: 'blur'}],
                carModel: [{required: true, message: '不能为空', trigger: 'blur'}],
                displacement: [{required: true, message: '不能为空', trigger: 'blur'}],
                seatNumber: [{required: true, validator: positiveIntegerNum, trigger: 'blur'}],
                clutch: [{required: true, message: '不能为空', trigger: 'blur'}],
            }
        }
    },
    props: ['API'],
    computed: {
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    methods: {
          // 删除颜色标签
          handleCloseAddColor(tag) {
            this.aForm.color.splice(this.aForm.color.indexOf(tag), 1);
          },  
          // 删除颜色标签
          handleCloseEditColor(tag) {
            this.eForm.color.splice(this.eForm.color.indexOf(tag), 1);
          },    
          // 显示添加标签的按钮
          showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
              this.$refs.saveTagInput.$refs.input.focus();
            });
          },
            // 新建-添加颜色
          addInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
              this.aForm.color.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = '';
          },
          // 编辑-添加颜色
          editInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
              this.eForm.color.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = '';
          },

        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            formatColor(row, column) {
                return row.color.join('<br/>')
            },
            // 解析json数组/字符串
            parseJsonArray(jsonArray) {
                if (!typeof jsonArray == "string") return '--'
                if (!/^\[.*\]$/.test(jsonArray)) return jsonArray
                return JSON.parse(jsonArray).join('<br/>')
            },
        // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "car_model_uuid": row.carModelUuid
                }
            }
            this.$common._post('carMoldeLibrary/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data)
                this.picUrlList = this.eForm.brandImage// 图片上传预览
                this.eForm.carModelUuid = row.carModelUuid
                if (typeof this.eForm.color != 'string' || this.eForm.color == "") {
                    this.eForm.color = []
                    return
                } 
                if (!/^\[.*\]$/.test(this.eForm.color)) {
                    this.eForm.color = [this.eForm.color]
                } else {
                    this.eForm.color = JSON.parse(this.eForm.color)
                }
            })
        },
        // 编辑保存
        editSave(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认修改?', '提示', {})
                        .then(() => {
                            this.eForm.picUrl = this.picUrlList
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                    "car_model_uuid": this.eForm.carModelUuid,
                                    "brand": this.eForm.brand,
                                    "carSeries": this.eForm.carSeries,
                                    "modelYear": this.eForm.modelYear,
                                    "color": JSON.stringify(this.eForm.color),
                                    "carModel": this.eForm.carModel,
                                    "displacement": this.eForm.displacement,
                                    "clutch": this.eForm.clutch,
                                    "seatNumber": +this.eForm.seatNumber,
                                    "brandImage": this.eForm.picUrl, //上传图
                                }
                            }
                            this.$common._post('carMoldeLibrary/edit', json, this, data => {
                                this.editDialog = false
                                this.$message({ type: 'success', message: data.description })
                                this.$refs[formName].resetFields()
                                this.getList()
                            })
                        }).catch(() => {})
                }
            })
        },
        addItem() {
            this.aForm = {
                brand: '',
                color: '',
                modelYear: '',
                carModel: '',
                carSeries: '',
                displacement: '',
                seatNumber: '',
                clutch: '',
                color: [],
                picUrl: '',
            }
            this.picUrlList = ''
            this.addDialog = true
        },
        addSave(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认添加?', '提示', {})
                        .then(() => {
                            this.aForm.picUrl = this.picUrlList
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                    "brand": this.aForm.brand,
                                    "carSeries": this.aForm.carSeries,
                                    "color": JSON.stringify(this.aForm.color),
                                    "modelYear": this.aForm.modelYear,
                                    "carModel": this.aForm.carModel,
                                    "displacement": this.aForm.displacement,
                                    "seatNumber": +this.aForm.seatNumber,
                                    "clutch": this.aForm.clutch,
                                    "brandImage": this.aForm.picUrl,
                                }
                            }
                            this.$common._post('carMoldeLibrary/insert', json, this, data => {
                                this.addDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                })
                                this.getList()
                            })
                        }).catch(null)
                }
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                    "car_model_uuid": row.carModelUuid
                }
            }
            this.$common.deleteItem(this.API + 'carMoldeLibrary/delete', this, json)
        },
        // 重置表单
        resetFields(formName, picFormName){
            this.$refs[formName].resetFields()
            picFormName && this.$refs[picFormName].clearFiles()
            this.eForm = {}
            this.picUrlList = null
        },
        // 上传成功
        onSuccess(res, file, fileList) {
            this.picUrlList = res.body.attachment_url
            this.$message('上传成功!')
        },
        // 导出
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelCarModel', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        // 分页
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(brand = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "brand": brand,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('carMoldeLibrary/list', json, this, data => {
                this.total = data.total
                this.tableData = data.list
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
        
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
    }
}
</script>

<style scoped lang="scss">
.avatar-uploader {
    .avatar {
        width: 100px;
        height: 100px;
    }
}

.avatar-uploader-icon {
  font-size: 20px;
  color: #999;
  width: 100px;
  height: 100px;
  line-height: 100px;
  text-align: center;
  border: 1px dashed #ccc;
}

.el-form-item__content {
    .button-new-tag,
    .el-tag+.el-tag {
        margin-left: 10px;
    }
}

.el-input--mini {
    margin-left: 10px;
    .el-input__inner {
        height: 28px;
    }
}
.viewImg {
    width: 60px;
    height: 60px;
}
</style>



